import { faMinus, faPlus, faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import { Table, Col, Modal, Button, Form } from "react-bootstrap";
import { numberWithCommas } from "../utils/utils";

const ModalKeranjang = ({
  showModal,
  handleClose,
  keranjangDetail,
  jumlah,
  keterangan,
  tambah,
  kurang,
  changeHandler,
  handleSubmit,
  totalHarga,
  hapusPesanan,
}) => {
  if (keranjangDetail) {
    return (
     
      <Modal show={showModal} onHide={handleClose}>
        <Form onSubmit={handleSubmit}>    
          <Modal.Header closeButton>
            <Modal.Title>Desksripsi Product</Modal.Title>
          </Modal.Header>
         
          <Modal.Body>  
            <Table borderless>
              <tr>
                <th>Nama Product</th>
                <td>:</td>
                <td colSpan="2">{keranjangDetail.product.nama} </td>
              </tr>
              <tr>
                <th>Harga Product</th>
                <td>:</td>
                <td colSpan="2">
                  {" "}
                  <strong>
                    Rp. {numberWithCommas(keranjangDetail.product.harga)})
                  </strong>
                </td>
              </tr>
              <tr>
                <th>Jumlah</th>
                <td>:</td>
                <td colSpan="2">
                  <Button
                    variant="primary"
                    size="sm"
                    className="mr-2"
                    onClick={() => kurang()}
                  >
                    <FontAwesomeIcon icon={faMinus} />
                  </Button>

                  <strong>{jumlah}</strong>

                  <Button
                    variant="primary"
                    size="sm"
                    className="ml-2"
                    onClick={() => tambah()}
                  >
                    <FontAwesomeIcon icon={faPlus} />
                  </Button>
                </td>
              </tr>
              <tr>
                <th>Harga Satuan</th>
                <td>:</td>
                <td colSpan="2">
                  <Form.Control
                    as="textarea"
                    rows="3"
                    name="keterangan"
                    placeholder="Contoh : Pedes, Nasi Setengah"
                    value={keterangan}
                    onChange={(event) => changeHandler(event)}
                  />
                </td>
              </tr>
            </Table>
            
            
          </Modal.Body>
          <Modal.Footer>
          <Col className="ml-n2">
              <Button
                variant="danger"
                onClick={() => hapusPesanan(keranjangDetail.id)}
              >
                <FontAwesomeIcon icon={faTrash} /> Hapus Pesanan
              </Button>
            </Col>

            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" type="submit" className="mr-4">
              Simpan
            </Button>
    
          <p className="float-right">
              <br></br>
              Total Harga :
              <p>
                <strong>Rp. {numberWithCommas(totalHarga)}</strong>
              </p>
            </p>
            <br></br>
          
          </Modal.Footer>
        
          </Form>
        </Modal> 
    );
  } else {
    return (
      <Modal show={showModal} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Kosong</Modal.Title>
        </Modal.Header>
        <Modal.Body>Kosong</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
};

export default ModalKeranjang;
